@import url("https://fonts.googleapis.com/css2?family=Almarai:wght@300&family=Arvo&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Alegreya:ital,wght@1,800&family=Almarai:wght@300&family=Graduate&display=swap");

@import url("./fonts/font.css");
html {
  scroll-behavior: smooth;
}

body {
  background-color: #f4f5f4;
  margin: 0;
  font-size: 10px;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body ::selection {
  background-color: #b9bebe86;
}
/* ===== Scrollbar CSS ===== */
/* Firefox */
* {
  scrollbar-width: thin;
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 25px;
}

*::-webkit-scrollbar-track {
  border-inline: solid 2px black;
}

*::-webkit-scrollbar-thumb {
  background-color: #b9bebe;
  border-radius: 5px;
  border: 3px solid #000000;
}
.app-body {
  opacity: 0;
  animation: fadeIn 0.5s ease-in forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
